@charset "utf-8";

$colortheme: #2dd8f3;

//Grid Setting
.AnsweringTF-Grid-wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 1;
  overflow: hidden;
}

.AnsweringTF-left {
  grid-column: 1/2;
  grid-row: 2/3;
  background-color: yellow;
  height: 100vh;
  overflow: hidden;
}
.AnsweringTF-right {
  grid-column: 2/3;
  grid-row: 2/3;
  background-color: $colortheme;
  //background-image: url("./Group_815.svg");
  background-repeat: no-repeat;
  background-position: 57% 0.5%;
  background-size: 52%;
  height: 100vh;
  overflow: hidden;
}
////////////////////////////////////////////////////////////////

//Left Grid component setting

.AnsweringTF-left-inline {
  /*display: flex;
  align-items: center;
  justify-content: center;*/
}
////////////////////////////////////////////////////////////////

//The quetion part

.AnsweringTF-question-box {
  align-self: center;
  flex-basis: center;
  margin: auto;
  height: 100%;
}

//hhhhhhhhhhhhhhhhhhhhhhhhhhhhhhh
.AnsweringTF-question-content {
  margin: auto;
  text-align: center;
  color: red;
  font-size: 6em;
  align-self: center;
  flex-basis: center;
  word-wrap: break-word;
  width: 100%;
  padding-top: 30%;
  flex-grow: 1;
  flex-shrink: 1;
}
////////////////////////////////////////////////////////////////

.AnsweringTF-Question-text-box {
  align-self: flex-end;
  flex-basis: flex-end;
  padding: 10%;
}

.AnsweringTF-question-text {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 0;
  color: black;
  font-size: 1.5em;
}

//////////hhhhhhhhhhhhhhhhhhhh
.AnsweringTF-left-flex {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  height: 100%;
  align-content: space-evenly;
  flex-grow: 1;
}

.AnsweringTF-back-button {
  margin-top: 4%;
  margin-left: 2%;
  padding: 2% 4%;
  background: #a70cff;
  font-size: 3em;
  color: white;
  border: 0;
  border-radius: 25px;
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  outline: 0;
  flex-grow: 1;
  flex-shrink: 1;
  transition-duration: 0.3s;

  &:hover {
    outline: 0;
  }

  &:active {
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.45),
      -10px -10px 10px rgba(255, 255, 255, 0.45);
    outline: 0;
    transition-duration: 0.3s;
  }
}

//The icon div

.AnsweringTF-question-icon-box {
  flex-wrap: nowrap;
}

.AnsweringTF-left-icon-box {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: auto;
  padding-top: 20%;
  padding-bottom: 0;
  flex-grow: 1;
  flex-shrink: 1;
}

.AnsweringTF-question-icon1 {
  padding: 0.5em;
  background-color: white;
  border-radius: 25px;
  color: #858585;
  font-size: 2em;
  border: 4px solid $colortheme;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.45),
    -10px -10px 10px rgba(255, 255, 255, 0.45);
}

.AnsweringTF-question-icon2 {
  padding: 0.5em;
  background-color: white;
  border-radius: 25px;
  color: #858585;
  font-size: 2em;
  margin-left: 2%;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.45),
    -5px -5px 5px rgba(255, 255, 255, 0.45);
}
////////////////////////////////////////////////////////////////

//Right Grid component setting
.AnsweringTF-Timer {
  position: absolute;
  top: 4.7%;
  left: 64.3%;
}

.AnsweringTF-Right-timer-background-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0;
}

div.background-wrapper {
  position: absolute;
}

.AnsweringTF-right-timer-background {
  height: 52%;
  width: 52%;
}

.AnsweringTF-muti-wrapper {
  //margin-top: 1%;
  margin-left: 1%;
  margin-right: 1%;
  margin-bottom: 4%;
  max-height: 55vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3%;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
}

.AnsweringTF-button {
  width: 30%;
  max-height: 50em;
  margin-top: 10%;
  flex-shrink: 1;
  flex-grow: 1;
  //height: 2.3em;
  color: red;
  font-size: 5em;
  text-align: center;
  padding: 2%;
  border-radius: 50px;
  border: 7px solid $colortheme;
  background: white;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.45),
    -10px -10px 10px rgba(255, 255, 255, 0.45);
  transition-duration: 0.3s;
  outline: none;

  &:hover {
    box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.45),
      -20px -20px 20px rgba(255, 255, 255, 0.45);
    outline: none;
  }

  &:active {
    outline: none;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.45),
      -10px -10px 10px rgba(255, 255, 255, 0.45);
    background: rgb(202, 245, 252);
    transition-duration: 0.3s;
  }
}

.true {
  color: #53ee58;
}

.false {
  color: red;
}

